.products-page {
  padding: 2rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

/* YEN KURAL */
.products-grid .full-width {
    grid-column: 1 / -1; /* Elementin tm stunlar kaplamasn salar */
}