/* Genel Sayfa Stilleri */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f9fa;
}

main {
  padding: 1rem 2rem;
}

a {
  text-decoration: none;
}

.loading-container, .error-container {
    text-align: center;
    padding: 3rem;
    font-size: 1.2rem;
}
.error-container {
    color: #dc3545;
}

/* === SEPET N KAYDIRMA DZELTMES BURADA === */

.app-wrapper {
  position: relative;
  /* Yatay tamay ve kaydrma ubuunu engellemek iin bu satr ok nemli */
  overflow-x: hidden; 
}

.main-content {
  background-color: #f8f9fa;
  min-height: 100vh;
  /* margin-right ve transform iin gei efekti */
  transition: margin-right 0.4s ease-in-out, transform 0.4s ease-in-out;
}

/* Sepet akken ana ieriin sanda boluk brakarak sola kaydr */
.app-wrapper.sidebar-active .main-content {
  /* transform kullanmak yerine margin veriyoruz */
  margin-right: 400px; /* Sidebar genilii kadar */
}